<template>
  <v-container mb-12>
    <entity-view
      v-bind:itemSchemas="itemSchemas"
      v-bind:select="select"
      v-bind:items="items"
      v-bind:title="title"
      v-bind:pagination="pagination"
      v-bind:externalActions="externalActions"
      v-on:items-refresh="itemsRefresh"
      v-on:item-insert="itemCreate"
      v-on:item-update="itemUpdate"
      v-on:item-delete="itemRemove"
      v-on:item-action="itemAction"
      v-on:page-change="pageChange"
    >
      <template v-slot:icon="item">
        <v-icon v-if="item.value" small color="success"> mdi-check-circle </v-icon>
        <v-icon v-if="!item.value" small color="error"> mdi-close-circle </v-icon>
      </template>
    </entity-view>
  </v-container>
</template>

<script>
import EntityView from "../components/EntityView.vue";
export default {
  components: { EntityView },
  data: () => ({
    title: "空间展示",
    select: true,
    pagination: {
      current: 1,
      pageCount: 1,
      pageSize: 10,
    },
    itemSchemas: [
      {
        name: "status",
        text: "状态",
        value: "status",
        type: "bool",
        align: "start",
        isKey: false,
        readOnly: true,
        sortable: false,
        oninsert: false,
        onupdate: false,
        ondelete: true,
        template: "icon",
        hasTemplate: true,
      },
      {
        name: "Name",
        text: "名称",
        value: "name",
        type: "string",
        oninsert: true,
        onupdate: true,
        ondelete: false,
      },
      {
        name: "spaceName",
        text: "所属空间",
        value: "spaceName",
        type: "string",
        oninsert: true,
        onupdate: true,
        ondelete: false,
      },
      {
        name: "switch",
        text: "开关",
        value: "switch",
        type: "string",
        oninsert: true,
        onupdate: true,
        ondelete: false,
      },
      {
        name: "time",
        text: "开启时间",
        value: "time",
        type: "string",
        oninsert: true,
        onupdate: true,
        ondelete: false,
      },
      {
        name: "totalTime",
        text: "开启总时间",
        value: "totalTime",
        type: "string",
        oninsert: true,
        onupdate: true,
        ondelete: false,
      },
      {
        name: "updated",
        text: "最近上报时间",
        value: "updated",
        type: "string",
        oninsert: true,
        onupdate: true,
        ondelete: false,
      },
    ],
    externalActions: [
      {
        name: "TestConnection",
        value: "testconnection",
        oninsert: true,
        onupdate: true,
        ondelete: false,
        color: "Primary",
      },
    ],
    items: [],
  }),
  mounted() {
    this.updateComponent();
  },
  methods: {
    updateComponent() {
      this.refresh();
    },
    refresh() {
      let count = 20;
      this.items = [];
      for (let index = 0; index < count; index++) {
        let item = {
          selected: true,
          status: Math.random() > 0.5 ? true : false,
          name: "教室" + index,
          spaceName: "空间" + index,
          switch: Math.random(0, 1) > 0.5 ? "关闭" : "打开",
          time: Math.ceil(Math.random() * 100),
          totalTime: Math.ceil(Math.random() * 100),
          updated: Math.ceil(Math.random() * 10) + "分钟",
        };
        this.items.push(item);
      }
    },
    itemsRefresh() {
      this.refresh();
    },
    itemCreate(item) {
      let requestData = item;
      const result = this.$http.post(
        this.$config.BackgroundService + "/api/data/connection",
        requestData
      );
      this.loading = true;
      result.then(
        (res) => {
          if (res.data.success) {
            this.$message.info({
              type: "success",
              message: "The users is refreshed",
            });
            this.refresh();
          } else {
            this.$message.error({
              type: "failed",
              message: res.data.info,
            });
          }
          this.loading = false;
        },
        (err) => {
          this.loading = false;
          console.log(err);
          this.$message.error(err);
        }
      );
      return result;
    },
    itemUpdate(item) {
      let requestData = item;
      const result = this.$http.put(
        this.$config.BackgroundService + "/api/data/connection",
        requestData
      );
      this.loading = true;
      result.then(
        (res) => {
          if (res.data.success) {
            this.$message.info({
              type: "success",
              message: "The users is refreshed",
            });
            this.refresh();
          } else {
            this.$message.error({
              type: "failed",
              message: res.data.info,
            });
          }
          this.loading = false;
        },
        (err) => {
          this.loading = false;
          console.log(err);
          this.$message.error(err);
        }
      );
      return result;
    },
    itemRemove(item) {
      const result = this.$http.delete(
        this.$config.BackgroundService + "/api/data/connection/" + item.id
      );
      this.loading = true;
      result.then(
        (res) => {
          if (res.data.success) {
            this.$message.info({
              type: "success",
              message: "The users is deleted",
            });
            this.refresh();
          } else {
            this.$message.error({
              type: "failed",
              message: res.data.info,
            });
          }
          this.loading = false;
        },
        (err) => {
          this.loading = false;
          console.log(err);
          this.$message.error(err);
        }
      );
      return result;
    },
    testConnection(item) {
      let requestData = item;
      const result = this.$http.post(
        this.$config.BackgroundService + "/api/jdbc/connect",
        requestData
      );
      this.loading = true;
      result.then(
        (res) => {
          if (res.data.success) {
            this.$message.info({
              type: "success",
              message: "The users is refreshed",
            });
            this.refresh();
          } else {
            this.$message.error({
              type: "failed",
              message: res.data.info,
            });
          }
          this.loading = false;
        },
        (err) => {
          this.loading = false;
          console.log(err);
          this.$message.error(err);
        }
      );
      return result;
    },
    pageChange() {
      return null;
    },
    itemAction(name, item) {
      if (name === "TestConnection") {
        this.testConnection(item);
      }
    },
  },
};
</script>

<style>
.list {
  max-width: 2000px;
}
.container {
  max-width: 2000px;
  margin: 0px;
}
</style>
