<template>
  <v-main scroll class="overflow-y-auto pa-2">
    <v-card>
      <!-- <v-card-title>
        {{ title }}
        <v-spacer></v-spacer> -->
      <!--        <v-text-field-->
      <!--            v-model="search"-->
      <!--            append-icon="mdi-magnify"-->
      <!--            label="Search"-->
      <!--            single-line-->
      <!--            hide-details-->
      <!--        ></v-text-field>-->
      <!-- </v-card-title> -->
      <v-data-table
        :headers="headers"
        :items="items"
        :attrs="attrs"
        :show-select="select"
        hide-default-footer
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>{{ title }}</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-btn
              class="mx-2"
              fab
              dark
              color="primary"
              small
              v-on:click.stop="showDialog(undefined, 'insert')"
            >
              <v-icon dark> mdi-plus </v-icon>
            </v-btn>
            <v-btn
              class="mx-2"
              fab
              dark
              color="primary"
              small
              v-on:click.stop="refresh()"
            >
              <v-icon dark> mdi-refresh </v-icon>
            </v-btn>
          </v-toolbar>
        </template>
        <template
          v-for="(h, index) in itemSchemas"
          v-slot:[`item.${h.value}`]="{ item, header, value }"
        >
          <slot v-if="h.hasTemplate" :name="h.template" v-bind:value="value"></slot>
          <div v-if="!h.hasTemplate" :key="index">
            {{
              header.formatter == undefined
                ? value
                : header.formatter(item, value)
            }}
          </div>
        </template>
        <template v-slot:[`item.action`]="{ item }">
          <v-hover v-slot="{ hover }">
            <v-row align="center" justify-space-around no-gutters>
              <v-spacer></v-spacer>
              <v-col class="d-flex justify-end">
                <v-btn
                  class="div-space"
                  fab
                  outlined
                  x-small
                  :class="hover ? '' : 'd-hidden'"
                  color="primary"
                  v-on:click.stop="showDialog(item, 'update')"
                >
                  <v-icon>mdi-lead-pencil</v-icon>
                </v-btn>
                <v-btn
                  class="div-space"
                  fab
                  outlined
                  x-small
                  :class="hover ? '' : 'd-hidden'"
                  color="primary"
                  v-on:click.stop="showDialog(item, 'delete')"
                >
                  <v-icon>mdi-trash-can</v-icon>
                </v-btn>
                <slot name="expanded-action" v-bind:item="item"> </slot>
              </v-col>
            </v-row>
          </v-hover>
        </template>
      </v-data-table>
      <template>
        <div class="text-center">
          <v-pagination
            v-model="this.pagination.current"
            :length="this.pagination.pageCount"
            :total-visible="7"
            @input="onPageChange"
            circle
          ></v-pagination>
        </div>
      </template>
    </v-card>
    <input-dialog
      ref="modifydialog"
      v-bind:operator="operator"
      v-bind:title="dialogTitle"
      v-bind:itemSchemas="itemSchemas"
      v-bind:actions="dialogActions"
      v-on:dialog-commit="saveDialog"
      v-on:dialog-action="invokeAction"
    >
    </input-dialog>
  </v-main>
</template>

<script>
import InputDialog from "./InputDialog";
export default {
  name: "EntityView",
  components: { InputDialog },
  props: [
    "itemSchemas",
    "items",
    "pagination",
    "searchData",
    "title",
    "externalActions",
    "select"
  ],
  data() {
    return {
      currentItem: "",
      headers: [],
      operator: "",
      dialogTitle: "",
      dialogActions: [],
      isOpen: "",
    };
  },
  mounted() {
    this.appendAction();
    this.loadHeaders();
  },
  methods: {
    appendAction() {
      this.itemSchemas.push({
        text: "Action",
        align: "center",
        value: "action",
      });
    },
    loadHeaders() {
      const headers = this.headers;
      this.itemSchemas.forEach((schema) => {
        if (schema.show == undefined || schema.show) {
          headers.push(schema);
        }
      });
    },
    showDialog(item, op) {
      this.operator = op;
      this.currentItem = item;
      this.isOpen = "true";
      if (this.operator == "insert") {
        this.dialogTitle = "Insert " + this.title;
      } else if (this.operator == "update") {
        this.dialogTitle = "Update " + this.title;
      } else if (this.operator == "delete") {
        this.dialogTitle = "Delete " + this.title;
      }
      this.filterExternalActions(op);
      this.$refs.modifydialog.showDialog(this.itemSchemas, this.currentItem);
    },
    saveDialog(operator, item) {
      if (operator == "insert") {
        this.insert(item);
      } else if (operator == "update") {
        this.update(item);
      } else if (operator == "delete") {
        this.delete(item);
      }
    },
    insert(item) {
      this.$emit("item-insert", item);
    },
    update(item) {
      this.$emit("item-update", item);
    },
    delete(item) {
      this.$emit("item-delete", item);
    },
    refresh() {
      this.$emit("items-refresh");
    },
    invokeAction(name, item) {
      this.$emit("item-action", name, item);
    },
    onPageChange(page) {
      this.pagination.current = page;
      this.$emit("page-change", this.pagination);
    },
    filterExternalActions(operator) {
      this.dialogActions = [];
      if (this.externalActions != undefined) {
        this.externalActions.forEach((element) => {
          if (element["on" + operator] == true) {
            this.dialogActions.push(element);
          }
        });
      }
    },
  },
};
</script>

<style scoped>
.div-space {
  margin-left: 4px;
}

.d-hidden {
  visibility: hidden;
}
</style>
